import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
var QQMapWX = require('../../utils/qqmap-wx-jssdk.min.js');
var qqmapsdk;
export default {
	data() {
		return {
			id: 0,
			data: '',
			user_info: '',
			token: '',
			position_address: '',
			latitude: '',
			longitude: ''
		}
	},
	onLoad(options) {
		//这里是修改地址的代码
		this.id = options.id ? options.id : 0;
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.token = app.globalData.user_info.token;
			if(!this.data){
				//请求异步ajax获取收货地址
				this.request({
					url: interfaces.addressDetail,
					data: {
						id: this.id
					},
					header: {
						token: this.token
					},
					method: 'POST',
					success: res => {
						console.log(res);
						if (res.code == 200) {
							this.data = res.data;
						} else {
							app.isLogin(res.code, res.msg);
						}
					}
				});
			}	
		} else {
			wx.reLaunch({
				url: '/pages/index/index'
			})
		}
	},

	methods: {
		//表单提交事件
		formSubmit(e) {
			var _this = this;
			var data = e.detail.value;

			if (data.linkman == '') {
				wx.showToast({
					title: '请输入联系人',
					icon: "none",
				});
				return;
			}

			if (!common.checkMobile(data.mobile)) {
				wx.showToast({
					title: '手机号码格式有误',
					icon: "none",
				});
				return;
			}

			if (data.position_address == '') {
				wx.showToast({
					title: '请获取服务地址',
					icon: "none",
				});
				return;
			}

			if (data.address == '') {
				wx.showToast({
					title: '请输入详细地址',
					icon: "none",
				});
				return;
			}

			data.dft = e.detail.value.dft ? 1 : 0;

			// 实例化API核心类
			qqmapsdk = new QQMapWX({
				key: 'WCYBZ-EWVC4-CC3U2-DZGZI-UWGK2-E2BMM'
			});

			qqmapsdk.reverseGeocoder({
				location: {
					latitude: data.latitude,
					longitude: data.longitude
				},
				success: addressRes => {
					data.position_city = addressRes.result.address_component.city;
					console.log(data);

					this.request({
						url: interfaces.addressEdit,
						data: data,
						header: {
							token: _this.token
						},
						method: 'POST',
						success: res => {
							console.log(res);
							if (res.code == 200) {
								wx.showToast({
									title: res.msg,
									icon: 'success'
								})
								setTimeout(function() {
									//返回地址信息列表
									wx.navigateBack({
										delta: 1
									})
								}, 1500);
							} else {
								app.isLogin(res.code, res.msg);
							}
						}
					});
				}
			})

		},

		open_map: function() {
			wx.chooseLocation({
				success: e => {
					this.data.position_address = e.address;
					this.data.latitude = e.latitude;
					this.data.longitude = e.longitude;
				},
				fail: err => {
					//console.log(err);
					if (err.errMsg === "chooseLocation:fail auth deny") {
						wx.showModal({
							title: '提示',
							content: '只有授权成功才能使用此功能，是否手动开启授权？',
							success: (res) => {
								if (res.confirm) {
									wx.openSetting({
										success(settingdata) {
											//console.log(settingdata)
											if (settingdata.authSetting['scope.userLocation']) {
												wx.showToast({
													title: '授权成功',
													icon: 'none',
													mask: true,
												})
											} else {
												wx.showToast({
													title: '授权失败',
													icon: 'none',
													mask: true,
												})
											}
										}
									})
								}
							}
						})
					}
				}
			});
		},
	}
}
